@import "../sass/k-sass-core-only";

///
/// Default BN-SAS-CORE configuration settings.
/// @:Option: with "_" will be compile to !important
/// @:null: with null to leave the value
///

.input-1 {
    #{$all-input} {
        color: color(main); } }


.input-2 {
    #{$all-input-hover} {
        color: color(link); } }

.input-3 {
    #{$all-input-active} {
        color: color(hover); } }

.input-4 {
    #{$all-button-hover} {
        color: color(link); } }
